.game-view {

	&__side-bar {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		text-align: center;
		padding-left: 0.5em;

		@media (max-width: 375px) {
			padding-left: 0;
			flex-direction: row;
		}
	}

	&__grid {
		text-align: center;
		align-self: center;
		padding-right: 0.5em;

		@media (max-width: 375px) {
			padding-right: 0;
		}
	}

	&__stats {

	}
}
