.tetris-page {
	display: flex;
	flex-direction: column;
	align-items: center;

	&__title {
		text-align: center;
		padding: 10px;
		font-size: 3em;
	}

	&__content {
		display: flex;
		flex-direction: row;
		flex-grow: 2;
		margin: 10px;
		background-color: #cecbcb;
		border-radius: 1em;
		padding: 1em;

		@media (max-width: 375px) {
			border-radius: 0;
			flex-direction: column;
		}
	}
}
